.movie{
    margin: 1rem;
    padding: 1rem;
    background-color: #AE6DAB;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 12px;
    text-align: center;
    color: white;
}

.movie h2 {
    font-size: 2rem;
    color: #422852;
}
  
.movie h3 {
    color: #EDE1F3;
    margin: 0;
    font-size: 1rem;
}
  